<template>
  <div class="home">
    <div class="mainBox">
      <div class="box"></div>
      <div class="line1">1</div>
      <div class="line1 line2">2</div>
    </div>
    <div class="input-btn">
      <input type="text" v-model="iptVal" class="ipt-style">
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      iptVal: ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home{
  width: 100vw;
  height: 100vh;
  background: rgb(245, 212, 212);
  display: flex;
  flex-direction: column;
}
.mainBox{
  flex: 1;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  padding: 0 30px;
}
.box{
  width: 100px;
  height: 200px;
  background: rgb(182, 157, 241);
}
.line1{
  width: 100%;
  margin-top: 100px;
  border-top: 1px solid rgb(46, 231, 160);
  /* box-sizing: border-box; */
}
.line2{
  border-top: 2px solid rgb(46, 231, 160);
}
.input-btn{
  height: 80px;
  background: #42b983;
}
.ipt-style{
  width: 100%;
  height: 100%;
  line-height: 80px;
}
</style>



